.footer {
  background: linear-gradient(135deg, #4b2c20, #1a0f0a);
  color: #fff3e6;
  padding: 3rem 1rem;
  font-family: 'Segoe UI', sans-serif;

  .top {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    text-align: center;

    h2 {
      font-size: 1.8rem;
      color: #ffd700;
      margin-bottom: 0.5rem;
    }

    p {
      font-size: 1rem;
      color: #e0d2b8;
    }

    .newsletter {
      max-width: 500px;

      h4 {
        color: #ffd700;
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
      }

      .form {
        margin-top: 1rem;
        display: flex;
        flex-direction: column;
        gap: 0.75rem;

        input {
          padding: 0.6rem 1rem;
          border: none;
          border-radius: 4px;
          font-size: 1rem;
          width: 100%;
        }

        button {
          background-color: #d35400;
          color: white;
          padding: 0.6rem;
          border: none;
          border-radius: 4px;
          font-size: 1rem;
          cursor: pointer;
          transition: background 0.3s;

          &:hover {
            background-color: #e67e22;
          }
        }
      }
    }
  }

  .bottom {
    margin-top: 2rem;
    text-align: center;

    p {
      margin-bottom: 1rem;
      font-size: 0.9rem;
      color: #fcebd5;
    }

    .social {
      display: flex;
      justify-content: center;
      gap: 1.2rem;

      a {
        font-size: 1.5rem;
        color: #ffd700;
        transition: transform 0.3s ease;

        &:hover {
          transform: scale(1.2);
          color: #ffcc00;
        }
      }
    }
  }

  // Desktop layout
  @media (min-width: 768px) {
    .top {
      flex-direction: row;
      justify-content: space-between;
      text-align: left;
    }

    .newsletter {
      .form {
        flex-direction: row;

        input {
          flex: 1;
        }

        button {
          flex-shrink: 0;
        }
      }
    }

    .bottom {
      text-align: center;
    }
  }
}
